.modal {

    &__body {
        position : fixed;
        top      : 0;
        right    : -100%;
        max-width: 440px;
        height   : 100%;

        background-color: #fff;
        border-left     : 1px solid #d9fff5;
        box-shadow      : -2px 0px 4px rgba(69, 79, 126, 0.02),
            16px 0px 52px rgba(14, 24, 80, 0.2);

        transition-property       : right;
        transition-duration       : 0.45s;
        transition-timing-function: ease-in-out;

        @media screen and (max-width: 320px) {
            padding-top   : 34px;
            padding-bottom: 40px;
            overflow-y    : auto;
        }
    }

    &__close {
        position: absolute;
        top     : 35px;
        right   : 470px;
        z-index : 10;

        &::after {
            content: url(../img/close.svg);
        }

        @media screen and (max-width: 320px) {
            position: absolute;
            top     : 16px;
            right   : 260px;
            z-index : 10;
        }
    }

    &__content {
        width : 90%;
        margin: 0 auto;
    }

    &__title {
        font-family: "TT Lakes Bold";
        font-size  : 1.5rem;
        color      : #1b1c21;
        margin     : 40px 0 24px 0;

        &::before {
            content      : "";
            position     : absolute;
            top          : 35px;
            left         : 0;
            width        : 3px;
            height       : 40px;
            background   : #41f6d7;
            border-radius: 0 2px 2px 0;

            @media screen and (max-width: 320px) {
                top : 70px;
                left: 0;
            }
        }
    }

    &__input,
    &__textarea {
        width        : 100%;
        margin-bottom: 1rem;
        padding      : 12px 16px;
        border       : 1px solid #eaeaea;
        border-radius: 8px;
        font-family  : "TT Lakes Regular";
        font-size    : 1rem;

        &::placeholder {
            font-family: "TT Lakes Regular";
            font-size  : 1rem;
            color      : #b5b6bc;
        }
    }

    &__input {
        height: 48px;
    }

    &__textarea {
        height: 118px;

        @media screen and (max-width: 320px) {
            height: 80px;
        }
    }

    &__text {
        margin-bottom: 1.5rem;
        font-family  : "TT Lakes Regular";
        font-size    : 0.75rem;
    }

    &__user-agreement {
        color: #ff3e79;
    }

    &__button.button {
        width           : 119px;
        height          : 32px;
        border-radius   : 16px;
        background-color: #ff3e79;
    }

    &__button {
        &:hover {
            opacity                   : 0.8;
            transition-delay          : 150ms;
            transition-duration       : 0.5s;
            transition-timing-function: ease-out;
        }

        &::after {
            content    : url(../img/dropdown.svg);
            margin-left: 0.6rem;
        }

        &:hover::after {
            margin-left               : 0.3rem;
            transition-delay          : 150ms;
            transition-duration       : 0.5s;
            transition-timing-function: ease-out;
        }
    }

    &__button span {
        text-transform: uppercase;
        font-family   : "TT Lakes Medium";
        font-size     : 0.75rem;
        color         : #ffffff;
        text-align    : right;
    }

    &__button-box {
        text-align: end;
    }

    &--style {
        position        : fixed;
        width           : 100%;
        height          : 100%;
        background-color: rgba(255, 255, 255, 0.98);
        top             : 0;
        right           : 0;
        z-index         : 10;

        transition-property       : all;
        transition-duration       : 0.2s;
        transition-timing-function: ease;
    }

    .appear__right {
        right: 0;
    }
}