@font-face {
  font-family: "TT Lakes Regular";
  src: url(../fonts/TTLakes-Regular.woff) format("woff");
}
@font-face {
  font-family: "TT Lakes Bold";
  src: url(../fonts/TTLakes-Bold.woff) format("woff");
}
@font-face {
  font-family: "TT Lakes Medium";
  src: url(../fonts/TTLakes-Medium.woff) format("woff");
}
html {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0 auto;
  padding: 0;
  font-family: "TT Lakes Regular";
  background-color: #f2f2f2;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

.display-none {
  display: none;
}

.restrictScroll {
  overflow: hidden;
}

.not-visible {
  visibility: hidden;
  opacity: 0;
}

.visible {
  visibility: visible;
  opacity: 1;
}

.page {
  width: 100%;
  display: flex;
  margin: 0 auto;
}

.nav__item a:link,
.nav__item a:visited {
  color: #7e7e82;
}

.page__general {
  margin-right: auto;
  padding: 42px 0 0 0;
  max-width: 1120px;
  background-color: #f8f8f8;
}
@media screen and (max-width: 768px) and (min-width: 321px) {
  .page__general {
    padding-top: 0;
    max-width: 768px;
  }
}
@media screen and (max-width: 320px) {
  .page__general {
    padding-top: 0;
    max-width: 320px;
  }
}

.container,
.main__header {
  margin: 0 auto;
  width: 90%;
}
@media screen and (max-width: 768px) and (min-width: 321px) {
