}

.button {
  border: none;
  outline: none;
  cursor: pointer;
  background: none;
}
.button__name {
  font-size: 1rem;
  margin-left: 1rem;
}
.button--call {
  background-image: url("../img/call.svg");
}
.button--chat {
  background-image: url("../img/chat.svg");
}
.button--profile {
  background-image: url("../img/profile.svg");
}
.button--burger-close {
  background-image: url("../img/burger-close.svg");
