.aside {
    margin-left: auto;

    @media screen and (max-width: 768px) and (min-width: 321px) {
        position        : fixed;
        top             : 0;
        left            : 0;
        width           : 100%;
        height          : 100%;
        z-index         : 10;
        background-color: rgba(255, 255, 255, 0.842);

        transition-property       : all;
        transition-duration       : 0.1s;
        transition-timing-function: ease;

        .appear__left {
            left: 0;
        }
    }

    @media screen and (max-width: 320px) {
        position        : fixed;
        top             : 0;
        left            : 0;
        width           : 100%;
        height          : 100vh;
        z-index         : 10;
        background-color: rgba(255, 255, 255, 0.842);

        .appear__left {
            left: 0;
        }
    }

    .page__aside {
        min-width       : 300px;
        height          : 100vh;
        position        : sticky;
        top             : 0;
        left            : 0;
        font-family     : "TT Lakes Medium";
        background-color: #ffffff;
        border-bottom   : 1px solid white;

        display        : flex;
        flex-direction : column;
        justify-content: space-between;

        @media screen and (max-width: 768px) and (min-width: 321px) {
            position: fixed;
            top     : 0;
            left    : -100%;
            width   : 320px;
            height  : 100%;
            box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02),
                16px 0px 52px rgba(14, 24, 80, 0.2);

            transition-property       : left;
            transition-duration       : 0.45s;
            transition-timing-function: ease-in-out;
        }

        @media screen and (max-width: 320px) {
            position: absolute;
            top     : 0;
            left    : -100%;
            width   : 320px;
            height  : 100%;
            overflow: auto;

            transition-property       : left;
            transition-duration       : 0.3s;
            transition-timing-function: ease-in-out;
        }
    }

    &__footer a:link,
    &__footer a:visited {
        color: #000000;
    }

    &__footer {
        margin       : 0 auto;
        width        : 85%;
        margin-bottom: 2rem;
    }

    &__buttons,
    &__email {
        margin-bottom: 1rem;
    }

    &__tel {
        font-family  : "TT Lakes Bold";
        font-size    : 1.5rem;
        margin-bottom: 2.5rem;
    }

    &__email,
    &__tel {
        transition-delay   : 50ms;
        transition-duration: 300ms;
        transition-property: opacity;

        &:hover {
            opacity: 0.5;
        }
    }
}

.menu {
    &__container {
        margin: 0 auto;
        width : 85%;
        height: 100%;
    }

    &__block {
        width: 30px;
    }

    &__header {
        height         : 86px;
        margin-bottom  : 32px;
        display        : flex;
        justify-content: space-between;
        align-items    : center;
    }

    &__item {
        margin-bottom      : 32px;
        color              : #000000;
        transition-duration: 300ms;
        transition-property: opacity;
    }

    &__item a:link,
    &__item a:visited {
        color: #000000;
    }

    &__item:hover {
        opacity: 0.5;
    }

    &__nav::before {
        content         : "";
        position        : absolute;
        top             : 105px;
        left            : 0;
        width           : 4px;
        height          : 40px;
        background-color: #41f6d7;
        border-radius   : 0 5px 5px 0;
    }

    &__section {
        max-height: 936px;

        display        : flex;
        flex-direction : column;
        justify-content: space-between;
    }
}

.button {

    &--call,
    &--chat {
        margin-right: 1rem;
    }

    &--burger-close,
    &--search,
    &--call,
    &--chat,
    &--profile {
        transition-delay   : 50ms;
        transition-duration: 300ms;
        transition-property: opacity;

        &:hover {
            opacity: 0.6;
        }
    }

    &__language {
        transition-delay   : 50ms;
        transition-duration: 300ms;
        transition-property: opacity;

        &:hover {
            opacity: 0.5;
        }
    }

    &--burger-close {
        @media screen and (max-width: 320px) {
            display: block;
        }
    }
}